/* 
    A este archivo le agregamos un guion bajo para decir que no se va a tener otro archivo css y compilarse ahi
    sino que va a compilarse en el archivo al que referenciemos ( o importemos) este archivo variables.
*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background-color: #4CBBB3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.header .logo__fest {
  color: #ffffff;
  font-size: 3rem;
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0px 0.3rem 1rem 0.5rem;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}

.navigator {
  display: flex;
  column-gap: 3rem;
  padding-bottom: 1.5rem;
}
.navigator .link__nav {
  color: #ffffff;
}
.navigator .link__nav:hover {
  color: #fef46d;
}
.navigator .link__nav--separador {
  border-left: 0.5px solid #ffffff;
  border-right: 0.5px solid #ffffff;
  padding: 0 2.5rem;
}

.video__container {
  position: relative;
  max-height: 80rem;
  overflow: hidden;
}
.video__container .info__video {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(51, 2, 18, 0.4);
}

.info__video--flex {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.title__video {
  color: #ffffff;
  font-weight: 900;
  font-size: 3rem;
}
.title__video .span__title--video {
  font-size: 1.3rem;
  background-color: #E3170A;
  padding: 6px;
  border-radius: 3px;
}

video {
  display: block;
  width: 100%;
}

@media (min-width: 768px) {
  .title__video {
    font-size: 5.5rem;
    letter-spacing: 2px;
  }
  .title__video .span__title--video {
    font-size: 1.3rem;
    padding: 10px;
  }
}
.section1 {
  display: grid;
  grid-template-rows: 1fr 1fr;
  margin: 2rem 1rem;
  text-align: center;
  column-gap: 15px;
}

.img__section1 {
  width: 100%;
}

@media (min-width: 768px) {
  .section1 {
    display: flex;
    justify-content: center;
  }
  .section1 .section1__item {
    max-width: 600px;
    text-align: left;
  }
  .img__section1 {
    min-width: 400px;
  }
}
.title__section1 {
  font-size: 3rem;
}

.fecha__section1 {
  color: #F53756;
}

.parrafo__section1 {
  font-size: 1.7rem;
}

.section2__container {
  width: 100%;
}

.section2 {
  background-color: #F53756;
  padding-bottom: 4rem;
}

.title__section2 {
  text-align: center;
  padding: 3rem;
  color: #ffffff;
}

.escenarios {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 3rem;
}

.escenarios__viernes, .escenarios__sabados {
  width: 95%;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  row-gap: 3rem;
}

.escenario {
  width: 100%;
}

.table__center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tabla__viernes1, .tabla__sabado1 {
  background-color: #fef46d;
  padding: 1rem;
}

.tabla__viernes2, .tabla__sabado2 {
  background-color: #4CBBB3;
  padding: 1rem;
}

table {
  width: 100%;
}

tr {
  padding: 1.2rem;
}

.tbody-bandas {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

td > span {
  font-weight: bold;
}

.title__dia {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 400;
}

@media (min-width: 768px) {
  .escenarios {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
  }
  .escenarios .escenarios__viernes, .escenarios .escenarios__sabados {
    display: flex;
    flex-direction: row;
    column-gap: 4rem;
    max-width: 800px;
  }
}
.title__section3 {
  text-align: center;
  font-size: 2.5rem;
}

.galeria-imagenes > li {
  list-style: none;
}

.contenedor > ul {
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  column-gap: 0.5rem;
  margin: 0 0.5rem;
}
.contenedor > ul.li {
  width: 100%;
}

img[data-imagen-id] {
  width: 100%;
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 0;
  margin: auto;
  display: grid;
  place-items: center;
  justify-content: center;
}
.overlay img {
  width: 100%;
  height: 70%;
}

.btn-cerrar {
  background-color: #ffffff;
  padding: 1rem 1.5rem;
  border-radius: 50%;
  transition: 0.5s all;
  text-align: center;
}
.btn-cerrar:hover {
  cursor: pointer;
  background-color: #F53756;
  color: #ffffff;
  font-weight: bold;
}

@media (min-width: 768px) {
  .contenedor > ul {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
    margin: 5rem 20%;
  }
}
.section4__container {
  margin: 5rem 0 0 0;
  padding: 5rem 0;
  background-color: #4CBBB3;
}

.title__section4 {
  font-size: 2.5rem;
  text-align: center;
  color: #ffffff;
}

.ticket__container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  row-gap: 5rem;
}

.day__container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
  align-content: center;
  width: 95%;
  max-width: 600px;
}

.DayPass {
  width: 100%;
  text-align: center;
  color: #ffffff;
  letter-spacing: 3px;
}

.table__ticket thead {
  display: flex;
  justify-content: center;
  background-color: #F53756;
  margin: 0;
  text-transform: uppercase;
  color: #ffffff;
}
.table__ticket tbody {
  display: grid;
  background-color: #ffffff;
  font-weight: 600;
  font-size: 1.4rem;
}

.tbody__ticket > tr {
  padding: 20px;
  border: 0.1px solid gray;
}

.footer__ticket {
  margin: 0;
  width: 100%;
  background-color: #752F97;
  padding: 1.5rem 0;
  color: #ffffff;
  text-align: center;
  font-size: 2.2rem;
}
.footer__ticket:hover {
  background-color: rgb(71, 32, 90);
  cursor: pointer;
}

@media (min-width: 768px) {
  .ticket__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
footer {
  padding: 4rem 1rem;
  background-color: #F53756;
  text-align: center;
}